@import-normalize;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
ul {
  list-style-type: none;
}

[data-theme='dark'] {
  --accent-bg-color: #bedbb0;
  --accent-text-color: #bedbb0;
  --main-text-color: #fff;
  --secondary-text-color: #fff;
  --buttons-color: #161616;
  --secondary-buttons-color: #121212;
  --buttons-bg-color: #bedbb0;
  --burger-color: #fff;
  --main-bg-color: #1f1f1f;
  --main-text-color-low-op: rgba(255, 255, 255, 0.5);
  --secondary-text-color-low-op: rgba(255, 255, 255, 0.5);
  --main-text-color-very-low-op: rgba(255, 255, 255, 0.1);
  --modal-border-color-low-op: rgba(255, 255, 255, 0.1);
  --modal-bg-color: rgba(21, 21, 21, 0.3);
  --need-help-bg-color: #1f1f1f;
  --modal-windows-bg-color: #151515;
  --main-aside-color: #121212;
  --main-header-color: #161616;
  --main-page-text-color: rgba(255, 255, 255, 0.5);
  --theme-wrapper: #151515;
  --theme-wrapper-border: #bedbb0;
  --theme-main-text-color: rgba(255, 255, 255, 0.8);
  --theme-text-color: rgba(255, 255, 255, 0.5);
  --theme-text-hover-color: #bedbb0;
  --logo-icon-color: #fff;
  --logo-icon-bg-color: #1f1f1f;
  --white-color: #fff;
  --white-color-low-op: rgba(255, 255, 255, 0.3);
  --black-color: #161616;
  --green-color: #bedbb0;
  --error-color: #ff2929;
  --priority-color-low: #8fa1d0;
  --priority-color-without: rgba(255, 255, 255, 0.3);
  --priority-color-medium: #e09cb5;
  --priority-color-high: #bedbb0;
  --scroll-thumb: rgba(72, 72, 72, 0.5);
  --scroll-bar: #121212;
  --logout-icon: #bedbb0;
  --modal-btn-hover: #9dc888;
  --modal-plus-icon: #fff;
  --modal-plus-icon-wrapper: #161616;
  --default-avatar: #151515;
  --add-column-btn: #121212;
